.app_skills {
  display: flex;
  flex: 1;
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media screen and (max-width: 900px) {
  .app_skills {
    transform: translateX(0.5rem);
  }
}

.app_skills-container {
  width: 80%;
  margin-top: 3rem;
  justify-content: center;
  align-items: center;
  display: flex;
}
@media screen and (max-width: 900px) {
  .app_skills-container {
    width: 100%;
    flex-direction: column;
  }
}

.app_skills-list {
  flex: 1;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  transform: translateX(2rem);
  margin-right: 5rem;
}
@media screen and (max-width: 900px) {
  .app_skills-list {
    margin-right: 0;
    justify-content: center;
    align-items: center;
    transform: translateX(0.2rem);
  }
}

.app_skills-item {
  flex-direction: column;
  text-align: center;
  justify-content: center;
  margin: 1rem;
  transition: all 0.3s ease-in-out;
}
.app_skills-item div {
  width: 90px;
  height: 90px;
  border-radius: 50%;
  background-color: var(--primary-white);
  transition: 0.3s ease-in-out;
}
.app_skills-item div img {
  height: 75%;
  width: 75%;
}
.app_skills-item div:hover {
  transform: scale(1.2);
}
@media screen and (min-width: 2000px) {
  .app_skills-item div {
    width: 150px;
    height: 150px;
  }
}
@media screen and (min-width: 900px) {
  .app_skills-item div {
    width: 70px;
    height: 70px;
  }
}
.app_skills-item p {
  font-weight: 500;
  margin-top: 0.5rem;
  color: var(--primary-white);
}
@media screen and (min-width: 2000px) {
  .app_skills-item {
    margin: 1rem 2rem;
  }
  .app_skills-item p {
    margin-top: 1rem;
  }
}/*# sourceMappingURL=Skills.css.map */